.mini-card{width:100%;background:var(--bg-secondary);border:1px solid var(--border);border-radius:var(--radius);padding:var(--spacing-16);position:relative}.mini-card__mini-info{display:flex;justify-content:space-between;align-items:center;margin-top:var(--spacing-8);padding-top:var(--spacing-8);border-top:1px solid var(--border);font-size:var(--font-size-xs)}.mini-card__mini-latest{color:var(--text-secondary)}.mini-card__mini-latest strong{color:var(--text-primary);font-weight:var(--font-weight-semibold)}.mini-card__mini-flags{display:flex;column-gap:var(--spacing-8);align-items:center}.mini-card__mini-flags .mini-card__had-issues-during-timeline-issues{font-size:var(--font-size-lg)}.mini-card__mini-status{display:inline-flex;align-items:center;justify-content:center;width:20px;height:20px;border-radius:50%;font-size:var(--font-size-sm);font-weight:var(--font-weight-bold)}.mini-card__mini-status.optimal{background:var(--success);color:var(--text-primary)}.mini-card__mini-status.high{background:var(--danger);color:var(--text-primary);animation:pulse-danger 2s infinite}.mini-card__mini-status.low{background:var(--warning);color:var(--bg-primary);animation:pulse-warning 2s infinite}.mini-card--mini{padding:var(--spacing-12)}.mini-card--mini:hover{border-color:var(--accent);cursor:pointer;transition:border-color .2s ease}.mini-card--mini .mini-card__header h4.mini-card__title{margin-top:0}.mini-card--loading::before{content:"";position:absolute;top:0;left:0;right:0;bottom:0;background:var(--surface-dark-dim);display:flex;align-items:center;justify-content:center;border-radius:var(--radius);z-index:10}.mini-card--loading::after{content:"Se încarcă...";position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);color:var(--text-secondary);font-size:var(--font-size-sm);z-index:11}.mini-card--error{border-color:var(--danger);background:var(--danger)10}.mini-card--error .mini-card__error{display:flex;align-items:center;justify-content:center;height:100px;color:var(--danger);font-size:var(--font-size-sm);text-align:center}.mini-card--no-data .mini-card__no-data{display:flex;flex-direction:column;align-items:center;justify-content:center;height:100px;color:var(--text-muted);font-size:var(--font-size-sm);text-align:center}.mini-card--no-data .mini-card__no-data-icon{font-size:var(--font-size-xl);margin-bottom:var(--spacing-8);opacity:.5}@keyframes pulse-danger{0%,100%{transform:scale(1);opacity:1}50%{transform:scale(1.1);opacity:.8}}@keyframes pulse-warning{0%,100%{transform:scale(1);opacity:1}50%{transform:scale(1.1);opacity:.8}}@media(max-width: 768px){.mini-card{padding:var(--spacing-12)}.mini-card--mini{padding:var(--spacing-8)}.mini-card--mini .mini-card__mini-info{font-size:var(--font-size-xxs)}.mini-card--mini .mini-card__had-issues-during-timeline-issues,.mini-card--mini .mini-card__mini-status{width:16px;height:16px;font-size:var(--font-size-xs)}}@media(prefers-contrast: high){.mini-card{border-width:2px}.mini-card__mini-status{border:2px solid var(--text-primary)}.mini-card__mini-status.optimal{border-color:var(--success)}.mini-card__mini-status.high{border-color:var(--danger)}.mini-card__mini-status.low{border-color:var(--warning)}}@media print{.mini-card{border:1px solid #000;background:#fff}.mini-card__mini-status.optimal::after{content:" (Optim)"}.mini-card__mini-status.high::after{content:" (Ridicat)"}.mini-card__mini-status.low::after{content:" (Scăzut)"}}